import React from "react";
import {getHighestScore, getScores} from "../../modules/scores";
import moment from "moment";
import {convertToEmojis} from "../../modules/uiutils";
import {CookieConsent} from "react-cookie-consent";

const GameStart = ({onStart}) => {
  const highestScore = getHighestScore();
  const latestScores = getScores();
  return (
    <div className="game-start">
      <h2> Learn Times Tables the Fun Way!</h2>
      {highestScore > 0 ? (
        <>
          <p>Welcome back to our amazing multiplication and division game!  We're so proud of you for achieving a high score of {convertToEmojis(getHighestScore())} in your previous attempts! You're doing an awesome job at mastering the times table from {convertToEmojis(1)} to {convertToEmojis(12)}. </p>
          <p>We're excited to see you again and urge you to try again and beat your top score! </p>
        </>): null}
      {latestScores.length > 0 ? (
          <>
            <p>Here's a quick summary of your previous attempts:</p>
            <ul>
              {latestScores.map((score, index) => (
                <li className="scores" key={index}> {convertToEmojis(score.score)} achieved {moment(score.dateTime).fromNow()}</li>))}
            </ul>
            <p>As you already know, each level has {convertToEmojis(20)} random multiple-choice questions. The game tests your multiplication and division skills, and you must choose the correct answer from {convertToEmojis(4)} options. You start with {convertToEmojis(3)} lives, and you gain an extra life after each level. Remember, incorrect answers will cost you a life, while correct answers earn you {convertToEmojis(10)} points.</p>
            <p>To add to the excitement, each level gets progressively harder, with the time to complete each question decreasing. This feature helps to build your confidence and speed in recalling the times table. </p>
            <p>So what are you waiting for? Let's start playing and beat your best score! </p>
          </>
        ) :
        <>
          <p>Welcome to our awesome game that helps you learn times tables in a fun way!  The game has 20 questions in each level, and you need to choose the right answer from 4 options. Each question has multiplication or division, and you have to think carefully before answering. </p>
          <p>You start with 3 lives , and you get an extra  after each level. You get  points for every correct answer, but if you get the answer wrong, you lose a life . The maximum score you can get is 1600 points. </p>
          <p>Each level gets harder, but it also gets more exciting!  You'll get faster at answering as you practice more, and you'll be a times table master in no time! </p>
          <p>So what are you waiting for? Let's start playing and have some fun while learning! </p>
        </>}
      <button onClick={onStart}>Start Now</button>
      <CookieConsent
        location="bottom"
        buttonText="Accept"
        cookieName="acceptCookies"
        style={{ background: "#252d4a" }}>
        This website uses cookies to save your scores. By using this website, you agree to our use of cookies.
      </CookieConsent>
    </div>
  );
}

export default GameStart;
