import React, {useEffect, useState} from 'react';

const messsages = [
    "Here is someone that  is really proud of you: ",
    "Well done! Maths can be as much fun as that: ",
    "Math might seem tough, but you're tougher! Just like that guy: ",
    "Don't worry if you make a mistake in math - just think of this: ",
    "Math might look intimidating at first, but once you start, it's just like that: ",
    "If math is giving you a headache, just think of that: ",
    "Math may be challenging, but it's also fun. Think of it like this: "
]

const giphy = {
    baseURL: "https://api.giphy.com/v1/gifs/",
    apiKey: "0UTRbFtkMxAplrohufYco5IY74U8hOes",
    tag: "animals",
    type: "random",
    rating: "g"
};

const giphyURL = encodeURI(
    giphy.baseURL +
    giphy.type +
    "?api_key=" +
    giphy.apiKey +
    "&tag=" +
    giphy.tag +
    "&rating=" +
    giphy.rating
);

const LevelAward = ({  }) => {
    const message = messsages[Math.floor(Math.random()*messsages.length)]
    const [giphyImage, setGiphyImage] = useState({});

    useEffect(() => {
        fetch(giphyURL)
            .then(res => res.json())
            .then(
                (result) => {
                    setGiphyImage(result.data.images.original);
                },
                (error) => {
                    console.error(error)
                }
            );
    }, []);

    const style = {
        backgroundImage: `url(${giphyImage.url})`,
        backgroundsize: 'contain',
        backgroundRepeat: 'no-repeat',
        height: `${giphyImage.height}px`,
        width: `100%`,
        marginTop: '10px'
    }

    return (
        <div>
            <div>{message}</div>
            <div style={style} />
        </div>
    );
};

export default LevelAward;