import Cookies from "js-cookie";

export const updateScores = (score) => {
  const now = new Date().toISOString(); // Get current date and time in ISO string format
  const existingScores = JSON.parse(Cookies.get('scores') || '[]'); // Get existing scores from cookie or initialize empty array

  existingScores.push({ score, dateTime: now });

  // If there are more than 10 scores, remove the oldest score
  if (existingScores.length > 10) {
    existingScores.shift();
  }

  Cookies.set('scores', JSON.stringify(existingScores));
};

export const getScores = () => {
   // Get existing scores from cookie or initialize empty array
  return JSON.parse(Cookies.get('scores') || '[]').reverse();
};

export const updateHighestScore = (score) => {
  const existingHighestScore = parseInt(Cookies.get('highestScore')) || 0; // Get existing highest score from cookie or initialize to 0
  const currentScore = parseInt(score);

  // If the current score is higher than the existing highest score, update the cookie
  if (currentScore > existingHighestScore) {
    Cookies.set('highestScore', currentScore);
  }
};

export const getHighestScore = () => {
  return parseInt(Cookies.get('highestScore')) || 0; // Get existing highest score from cookie or initialize to 0
}